<template>
  <div class="epub_box">
    <div class="epub_box_c" style="margin-top: 15px">
      <div class="read_left_box">
        <div class="read_left">
          <div class="title theme-title">商品详情</div>
          <div class="read_leftt1">
            <img class="read_leftt1_img" :src="detail.resourceCover" />
            <div class="title">
              <!-- <div class="book-label">课程</div> -->
              <span>{{ detail.resourceName }}</span>
            </div>
          </div>
          <div class="read_leftt2">
            <li>讲师：{{ detail.lecturer }}</li>
            <li>
              <div
                v-if="
                  (!showTime && showStartTime) || detail.isValidForever == 0
                "
              >
                <span>有效期：</span>
                <span class="ellipsis-aRow" v-if="detail.isValidForever != 0"
                  >截止到{{ formatDate(detail.validEndDate) || "-" }}</span
                >
                <span
                  style="margin-left: 10rpx"
                  v-if="detail.isValidForever == 0"
                  >永久</span
                >
              </div>
              <div
                v-if="showTime && showStartTime && detail.isValidForever != 0"
                style="color: red"
              >
                课程已到期
              </div>
              <div
                v-if="!showStartTime && detail.isValidForever != 0"
                style="color: red"
              >
                课程未开始
              </div>
            </li>
          </div>
        </div>
        <div class="read_left2">
          <div>
            <div class="radius">
              <div class="time">
                {{
                  learningTime < 60
                    ? learningTime
                    : (learningTime / 60).toFixed(1)
                }}
                <span style="font-size: 14px">{{
                  learningTime < 60 ? "分钟" : "小时"
                }}</span>
              </div>
              <div class="text">今日学习时长</div>
            </div>
            <div class="num">
              <span
                style="font-weight: bold; color: #2f7cfb; font-size: 25px"
                >{{ GetQuestion.userQuestion || "0" }}</span
              >/{{ GetQuestion.questionCount || "0" }}
            </div>
            <div class="label">
              <span>已做题目/总题数</span>
            </div>
          </div>
        </div>
      </div>
      <div class="video_box">
        <div class="video_name_row">
          <img class="img_box" src="@/assets/icon/shipin.png" />
          <div>{{ sectionName }}</div>
        </div>
        <div v-if="startVideoUrlshow" style="width: 90%; margin: 20px auto">
          <player
            style="width: 100%; height: 100%"
            ref="video_player"
            @reciveMessage="reciveMessage"
            @videoEnd="videoEnd"
            @videoPause="videoPause"
          ></player>
        </div>
        <div
          v-if="!startVideoUrlshow"
          style="text-align: center; margin-top: 150px; height: 650px"
        >
          <noData></noData>
          <div v-if="isShop" style="color: #999">课程已到期 / 课程未开始</div>
          <div v-if="!isShop" style="color: #999">未购买</div>
        </div>
      </div>
      <div class="list_box">
        <el-tabs class="tabs" v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="课程表" name="first"></el-tab-pane>
          <el-tab-pane label="试题" name="second"></el-tab-pane>
        </el-tabs>
        <div class="tabs_box" v-if="activeName == 'first'">
          <el-collapse
            v-model="course"
            @change="handleChange"
            v-if="!courseListshow"
          >
            <el-collapse-item
              v-for="(item, index) in courseList"
              :title="item.name"
              :key="index"
              :name="index + 1"
            >
              <div v-for="(items, indexs) in item.sections" :key="indexs">
                <div
                  class="row"
                  @click="startVideo(items, item.name, index)"
                  style="margin-top: 5px"
                >
                  <div
                    class="collapse"
                    :class="items.content == vid ? 'color' : ''"
                  >
                    {{ items.name }}
                  </div>
                  <div class="row">
                    <img src="@/assets/images/time.png" />
                    <span class="collapse">{{ xgetTime(items.duration) }}</span>
                  </div>
                </div>
              </div>
            </el-collapse-item>
          </el-collapse>
          <noData v-if="courseListshow"></noData>
        </div>
        <div class="tabs_box" v-if="activeName == 'second'">
          <div v-if="!papershow">
            <div v-for="(item, index) in paperList" :key="index">
              <div class="shiti_row">
                <div class="text">
                  <div>{{ item.paperName }}</div>
                  <div>
                    <span style="color: #2e9adb">{{ item.useNum }}</span
                    >/{{ item.questionNum }}
                  </div>
                </div>
                <div class="but" @click="anwser(item.id)">去答题</div>
              </div>
            </div>
          </div>
          <noData v-if="papershow"></noData>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { checkIsBuy } from "@/api/bookstore/bookstore";
import noData from "@/components/noData.vue";
import player from "@/views/willingLearn/bookstore/player.vue";
import { videourl } from "@/utils/config.js";
import {
  selectOcLexueResourceBookCourseById,
  selectOclexueResourceBookById,
  getLexueReadTime,
  selectTimeParameter,
  GetQuestionIsUse,
  updateLexuePlayedbackSchedule,
  saveProgress,
  getUserLastRead,
} from "@/api/willingLearn/index.js";
export default {
  components: { noData, player },
  computed: {},
  data() {
    return {
      videourl: videourl,
      activeName: "first",
      payType: "goods",
      courseList: [],
      courseListshow: false,
      paperList: [],
      course: [1],
      goodsId: this.$route.query.goodsId,
      detail: "",
      learningTime: 0,
      timer: null,
      GetQuestion: "",
      frequency: 1,
      currentTime: 0,
      duration: 0,
      startTime: 0,
      chapterName: "",
      videoData: {},
      vid: "",
      showTime: false,
      showStartTime: true,
      isShop: true, //判断购买
      sectionName: "",
      startVideoUrlshow: true,
      playState: false,
      papershow: false,
      courseCover: "",
    };
  },
  watch: {
    //监听路由变化
    $route() {},
  },
  created() {
    this.course[0] = localStorage.getItem("vidIndex")
      ? Number(localStorage.getItem("vidIndex"))
      : 1;
    this.loading = this.$loading({
      lock: true,
    });
    setTimeout(() => {
      this.loading.close();
      this.selectOclexueResourceBookByIds();
      // 获取乐学商品当天学习时长
      getLexueReadTime({ goodsId: this.goodsId }).then((res) => {
        this.learningTime = res.data || 0;
      });
    }, 500);
  },
  beforedestroy() {
    this.updatePlayedbackSchedule();
  },
  destroyed() {
    clearInterval(this.timer);
    this.timer = null;
  },
  mounted() {
    console.log(this.course);
    selectTimeParameter().then((res) => {
      this.frequency = res.data || 1;
    });
  },
  methods: {
    checkIsBuys() {
      //判断是否已经购买该商品
      this.payType = "goods";
      checkIsBuy({ relevanceId: this.goodsId, type: this.payType }).then(
        (res) => {
          if (res.data === "2") {
            this.$message.error("数据异常");
            return;
          }
          if (res.data === "1") {
            this.isShop = false;
            this.isPayGoods();
          } else {
            this.isShop = true;
            //课程数量
            this.selectOcLexueResourceBookCourseByIds();
            //试卷数量
            GetQuestionIsUse({ goodsId: this.goodsId }).then((res) => {
              if (res.data) {
                this.GetQuestion = res.data;
                this.paperList = res.data.paperInfo || [];
                this.papershow = false;
              } else {
                this.papershow = true;
              }
            });
          }
        }
      );
    },
    isPayGoods() {
      //走购买流程
      this.startVideoUrlshow = false;
      this.courseListshow = true;
      this.papershow = true;
      let title = "该文件需要购买后才能阅读，是否确认购买";
      this.$alert(`${title}`, "购买", {
        confirmButtonText: "立即购买",
        callback: (action) => {
          if (action === "cancel") {
            return;
          }
          let userId = this.$store.getters.userId;
          if (!userId) {
            this.$router.push({
              path: "/login",
              query: {
                redirectPath: "/onlineReading",
              },
            });
          } else {
            //判断是否购买该商品
            this.payType = "goods";
            checkIsBuy({
              relevanceId: this.goodsId,
              type: this.payType,
            }).then((res) => {
              console.log("是否已购买响应", res);
              if (res.data === "2") {
                this.$message.error("数据异常");
                return;
              }
              if (res.data === "1") {
                this.$router.push({
                  path: "/submitOrder/index",
                  query: {
                    id: this.$route.query.goodsId,
                    from: "bookstore",
                  },
                });
              } else {
                this.$message("该商品已购买无需重复购买");
              }
            });
          }
        },
      });
    },
    //播放时返回
    reciveMessage(e) {
      if (e) {
        console.log(e);
        if (e.name == "videoPlay") {
          if (!this.playState) {
            this.playState = true;
            this.updatePlayedbackSchedule();
          }
          this.currentTime = e.video.currentTime;
          this.duration = e.video.duration;
          this.startTime++;
          if (this.startTime == this.frequency * 60) {
            this.startTime = 0;
            saveProgress({
              goodsId: this.goodsId,
            }).then(() => {
              getLexueReadTime({ goodsId: this.goodsId }).then((res) => {
                this.learningTime = res.data || 0;
              });
              this.updatePlayedbackSchedule();
            });
          }
        }
      }
    },
    videoEnd(e) {
      if (e) {
        if (e.name == "videoEnd") {
          this.currentTime = e.video.currentTime;
          this.duration = e.video.duration;
          saveProgress({
            goodsId: this.goodsId,
          }).then(() => {
            getLexueReadTime({ goodsId: this.goodsId }).then((res) => {
              this.learningTime = res.data || 0;
            });
            this.updatePlayedbackSchedule();
          });
        }
      }
    },
    videoPause(e) {
      if (e) {
        if (e.name == "videoPause") {
          this.currentTime = e.video.currentTime;
          this.duration = e.video.duration;
          saveProgress({
            goodsId: this.goodsId,
          }).then(() => {
            getLexueReadTime({ goodsId: this.goodsId }).then((res) => {
              this.learningTime = res.data || 0;
            });
            this.updatePlayedbackSchedule();
          });
        }
      }
    },
    // 更新用户乐学模块播放记录
    updatePlayedbackSchedule() {
      updateLexuePlayedbackSchedule({
        chapterId: this.vid,
        chapterName: this.chapterName,
        sectionName: this.sectionName,
        goodsId: this.goodsId,
        playbackDuration: this.currentTime,
        playbackLocation:
          this.currentTime > 0 && this.duration > 0
            ? ((this.currentTime * 100) / (this.duration * 100)).toFixed(2)
            : 0,
      });
    },
    //商品列表
    selectOclexueResourceBookByIds() {
      let data = {
        id: this.goodsId,
      };
      selectOclexueResourceBookById(data).then((res) => {
        this.detail = res.data;
        let strEnd = this.detail.validEndDate;
        if (this.detail.isValidForever != 0) {
          if (new Date(strEnd).getTime() > Date.now()) {
            this.showTime = false;
          } else {
            this.showTime = true;
          }
          let Start = this.detail.validStartDate;
          if (new Date(Start).getTime() > Date.now()) {
            this.showStartTime = false;
          } else {
            this.showStartTime = true;
          }
        }
        this.checkIsBuys();
      });
    },
    //课程
    selectOcLexueResourceBookCourseByIds() {
      selectOcLexueResourceBookCourseById(this.goodsId).then((res) => {
        this.courseList = JSON.parse(res.data.course.chapters) || [];
        this.courseCover = res.data.course.cover || "";
        if (this.courseList.length > 0) {
          this.getUserLastReads();
          this.courseListshow = false;
        } else {
          this.courseListshow = true;
        }
      });
    },
    //首次加载
    getUserLastReads() {
      getUserLastRead(this.goodsId).then((res) => {
        let time = 0;
        //有播放记录
        if (res.data.length > 0) {
          let lastRead = res.data[0];
          lastRead.sectionName ? (this.sectionName = lastRead.sectionName) : "";
          lastRead.chapterName ? (this.chapterName = lastRead.chapterName) : "";
          lastRead.chapterId ? (this.vid = lastRead.chapterId) : "";
          time = lastRead.playbackDuration;
        }
        //没有历史播放
        if (res.data.length == 0) {
          //判断课程是否到开始时间
          if (this.showStartTime && !this.showTime) {
            this.sectionName = this.courseList[0].sections[0].name;
            this.chapterName = this.courseList[0].name;
            this.vid = this.courseList[0].sections[0].content;
            this.videoData = this.courseList[0].sections[0];
            time = 0;
          } else {
            this.startVideoUrlshow = false;
            return;
          }
        }
        setTimeout(() => {
          this.$refs.video_player.DmsPlayers(
            3,
            18,
            this.vid,
            this.courseCover,
            time
          );
        }, 200);
      });
    },
    //点击切换
    startVideo(item, chapterName, index) {
      if (!this.showStartTime) {
        this.$message({
          message: "课程未开始",
        });
        return;
      }
      if (this.showTime) {
        this.$message({
          message: "课程已过期",
        });
        return;
      }
      this.playState = false;
      this.vid = item.content;
      this.chapterName = chapterName;
      this.sectionName = item.name;
      setTimeout(() => {
        this.$refs.video_player.DmsPlayers(
          3,
          18,
          this.vid,
          this.courseCover,
          0
        );
      }, 200);
    },
    handleClick() {},
    handleChange() {},
    anwser(id) {
      if (!this.showStartTime) {
        this.$message({
          message: "课程未开始",
        });
        return;
      }
      if (this.showTime) {
        this.$message({
          message: "课程已到期",
        });
        return;
      }
      let url = this.$router.resolve({
        path: "/willingLearn/paper/testPaper",
        query: {
          id: id,
          goodsId: this.goodsId,
          teachProperty: this.detail.teachProperty,
        },
      });
      window.open(url.href, "_blank");
    },
    xgetTime(time) {
      let h = parseInt(time / 60 / 60);
      let m = parseInt(time / 60);
      let s = time % 60;
      let date =
        (h > 9 ? h : "0" + h) +
        ":" +
        (m > 9 ? m : "0" + m) +
        ":" +
        (s > 9 ? s : "0" + s);
      return date;
    },
    formatDate(time) {
      var date = new Date(time);
      var year = date.getFullYear(),
        month = date.getMonth() + 1, //月份是从0开始的
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day);
      //  + ' ' +
      // (hour < 10 ? '0' + hour : hour) + ':' +
      // (min < 10 ? '0' + min : min) + ':' +
      // (sec < 10 ? '0' + sec : sec);
      return newTime;
    },
  },
};
</script>
